import { Chart, Tooltip, Axis, Legend, Pie, Coord } from 'viser-react';
import * as React from 'react';
import { DataSet } from '@antv/data-set';


export default class PieChart extends React.Component {
  
  render() {
    const sourceData = [
      { item: '水票', count: 40 },
      { item: '书籍', count: 21 },
      { item: '寝室神器', count: 17 },
      { item: '租房', count: 13 },
      { item: '文具', count: 9 },
      { item: '电脑办公', count: 40 },
      { item: '游戏道具', count: 21 },
      { item: '体育用具', count: 17 },
      { item: '乐器', count: 13 },
      { item: '电器', count: 9 },
      { item: '装饰品', count: 13 },
      { item: '其他', count: 9 },
    ];
    
    const scale = [{
      dataKey: 'percent',
      min: 0,
      formatter: '.0%',
    }];
    
    const dv = new DataSet.View().source(sourceData);
    dv.transform({
      type: 'percent',
      field: 'count',
      dimension: 'item',
      as: 'percent'
    });
    const data = dv.rows;
    return (
      <Chart forceFit height={400} data={data} scale={scale}>
        <Tooltip showTitle={false} />
        <Coord type="theta" />
        <Axis />
        <Legend dataKey="item" />
        <Pie
          position="percent"
          color="item"
          style={{ stroke: '#fff', lineWidth: 1 }}
          label={['percent', {
            formatter: (val, item) => {
              return item.point.item + ': ' + val;
            }
          }]}
        />
      </Chart>
    );
  }
}